<template>
	<v-app id="inspired">
		<v-content>
			<v-container class="fill-height" fluid>
				<v-card elevation="3" max-width="400px" min-width="350px" class="mx-auto form-card">
					<v-img
						id="logo"
						class="white--text"
						contain
						height="80px"
						:src="logo"
					></v-img>
					<div class="header">Open Calendar</div>
					<div class="subheader">Log In or Sign Up</div>
					<v-form ref="form" v-model="valid" class="form">
						<v-text-field
							v-model="email"
							:rules="emailRules"
							label="E-mail"
							outlined
							required
							placeholder="E-mail"
						></v-text-field>
						<v-text-field
							:append-icon="pass ? 'mdi-eye' : 'mdi-eye-off'"
							:type="pass ? 'text' : 'password'"
							v-model="password"
							:rules="passwordRules"
							label="Password"
							outlined
							ref="password"
							required
							placeholder="Password"
							@click:append="pass = !pass"
						
						></v-text-field>
						<div class="actions">
							<v-btn
								depressed
								color="primary"
								class="mr-4"
								href="/auth/google/login"
							>
								<v-icon dark left>mdi-login</v-icon>
								
								Sign In with Google
							</v-btn>
							<v-btn
								outlined
								@click="validate"
							>
								Sign In
							</v-btn>
						</div>
					
					</v-form>
				</v-card>
			
			</v-container>
		</v-content>
	</v-app>
</template>
<style>
	::-webkit-scrollbar {
		width: 0;
	}
	
	body, html {
		max-height: 100vh;
	}
	
	#inspired {
		background: #bbdefb;
	}
	
	#logo {
		margin: 0 auto;
	}
	
	.header {
		text-align: center;
		font-size: 2em;
	}
	
	.subheader {
		text-align: center;
		font-size: 1.2em;
		color: #777;
	}
	
	.form-card {
		padding: 20px;
	}
	
	.form {
		padding: 20px 0;
	}
	
	.actions {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}
	
	.actions > * {
		margin: 0;
	}
</style>

<script defer>
    // set icon
    document.querySelector('link[rel*="icon"]').href = "https://ssl.gstatic.com/calendar/images/dynamiclogo/2x/cal_" + (new Date().getDate().toString().length === 2 ? new Date().getDate() : '0' + new Date().getDate().toString()) + "_v2.png"
    export default {
        name: 'App',

        components: {},
        data: () => ({
            pass: false,
            email: '',
            valid: false,
            password: '',
            passwordRules: [
                v => !!v || 'Password is required',
                v => /.{8,}/.test(v) || 'Password must contain at least 8 characters',
            ],
            emailRules: [
                v => !!v || 'E-mail is required',
                v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
            ],
            logo: "https://ssl.gstatic.com/calendar/images/dynamiclogo/2x/cal_" + (new Date().getDate().toString().length === 2 ? new Date().getDate() : '0' + new Date().getDate().toString()) + "_v2.png"
        }),
        methods: {
            async validate() {
                try {

                    if (this.$refs.form.validate()) {
                        let res = await (await fetch('/auth/mail/login', {
                            method: 'POST',
                            headers: {
                                'Accept': 'application/json',
                                'Content-Type': 'application/json'
                            },
                            body: JSON.stringify({username: this.$data.email, password: this.$data.password})
                        }))
                        if (res.status >= 400) {
														this.$data.password = '';
														this.$refs.form.validate()
                        } else {
                            window.location = '/app'
                        }
                    }
                } catch (e) {
                    window.location.reload();
                }

            },
        }
    };
</script>
